<template>
  <div class="sign_in-wrap">
    <div class="sign_in">
      <h2><img src="../public/images/login_logo.png"><span>肠癌早期筛查专业检测</span></h2>
      <div class="handler">
        <label class="tel_input">
          <input  type="tel" v-model="tel" @blur="checkTel"  placeholder="请输入大陆11位手机号码">
        </label>
        <div class="code clearfloat">
          <label>
            <input type="text" @blur="checkCode" v-model="code" placeholder="请输入手机验证码">
          </label>
          <button @click="sendValidateCode" v-text="vCodeData.text"></button>
        </div>
        <a href="javascript:;" @click="sure" class="btn" :class="{disable:!code_is_ok}">登录</a>
      </div>
    </div>

  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/login";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name: 'Login',
    data: function () {
      return {
        oldTel:'12345678910',
        // 表单内容
        tel:'',
        code:'',

        code_is_ok:false,
        vCodeData:{//验证码相关参数
          sending:0,
          s:60,
          text:'获取验证码',
          timer:null,
        }
      }
    },
    created: function () {

    },
    mounted(){
      document.querySelector('html').classList.remove('bg')
    },
    methods: {
      is_tel:function (phone) { // 判断是否是正确的手机号
        return !!phone.match(/^((\d{11})|^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$)$/);
      },
      sure:function () { // 更换手机号码
        var tel = this.tel.trim();
        if(!this.code_is_ok){return}
        switch (true){
          case (tel === ''):
            this.justTips('手机号码不能为空');
            return;
            break;
          case (!this.is_tel(tel)):
            this.justTips('手机号码格式不正确');
            return;
            break;
          case (this.code === ''):
            this.justTips('验证码不能为空');
            return;
            break;
          default:
            break;
        }
        if(this.code){
          this.$router.push('home')
        }
      },
      sendValidateCode:function () {
        if(this.vCodeData.sending){return}
        if(this.checkTel()){
          this.send();
        }
      },
      send:function () { // 倒计时
        clearInterval(this.vCodeData.timer);
        var self=  this;
        this.vCodeData.sending = 1;
        this.vCodeData.timer = setInterval(function () {
          self.vCodeData.s -= 1;
          self.vCodeData.text = self.vCodeData.s + 's';
          if(self.vCodeData.s <= 0){
            self.reset(self);
          }
        },1000);
        return true;
      },
      reset:function (self) { // 重置倒计时
        clearInterval(self.vCodeData.timer);
        self.vCodeData.text = '获取验证码';
        self.vCodeData.s = 60;
        self.vCodeData.sending = 0
      },
      checkTel:function () {
        var tel = this.tel.trim();
        switch (true){
          case (tel === ''):
            this.justTips('手机号码不能为空');
            return false;
            break;
          case (!this.is_tel(tel)):
            this.justTips('手机号码格式不正确');
            return false;
            break;
          default:
            break;
        }
        return true;
      },
      checkCode:function () { //验证码是否正确
        var code = this.code.trim();
        if(code){
          this.code_is_ok = true;
        }else {
          this.code_is_ok = false;
        }
      },
      justTips:function (str) {
        this.$store.commit('show_justTips', {
          str:str.trim(),
          show:true
        });
      }
    }
  }
</script>



