<template>
  <div style="padding-bottom: 3rem">
    <div class="commodity clearfloat center">
      <div class="f_left">
        <img src="../public/images/commodity_drawing.jpg">
      </div>
      <div class="f_right">
        <div class="f_right_title">{{commodity}}</div>
        <div class="price">
          价格:￥<span>{{totalPrice}}</span>
        </div>
        <div class="num-wrap">
          <span>购买数量</span>
          <p class="clearfloat">
            <span @click="reduceNum"><b>-</b></span>
            <input v-model="commodity_num" type="text" pattern="[0-9]*" @keyup="numInput"
                   @afterpaste="numInput">
            <span @click="addNum"><b>+</b></span>
          </p>
        </div>
      </div>
    </div>
    <section>
      <p class="subTitle">填写个人购买信息</p>
      <ul class="list-form">
        <li>
          <span><b class="required">*</b>姓名</span>
          <label>
            <input type="text" v-model="name" placeholder="请填写真实姓名" maxlength="15">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>手机号码</span>
          <label>
            <input type="text" v-model="tel" placeholder="请填写收货人手机号码" maxlength="11">
          </label>
        </li>
        <li class="textarea-wrap">
          <p class="choose_address">
            <span><b class="required">*</b>地区</span>
            <label @click="showPicker">
              <span class="justText">{{address}}</span>
              <!--<input v-show="!address" @focus="inputBlur" type="text" placeholder="请选择地区"  readonly  v-model="address" class="disable" >-->
            </label>
          </p>

          <p class="textarea">
            <label>
              <textarea v-model="specific_address" maxlength="50" placeholder="为方便寄送，请在此填写5个字以上的收货人详细地址"></textarea>
            </label>
          </p>
        </li>
      </ul>
      <p class="subTitle">支付方式</p>
      <ul class="list-form">
        <li>
          <span class="wx_icon"><b class=""><img src="../public/images/wx_icon.png"></b><span>微信支付</span></span>
          <label for="wx_payment">
            <input type="radio" v-model="pay" name="payment" id="wx_payment" value="wx_payment" class="hide-input">
            <span class="is-radio"></span>
          </label>
        </li>
      </ul>
    </section>
    <transition name="fade">
      <div class="picker-dialog" @click="hide_picker_dialog" v-show="show_picker_dialog">
        <mt-picker :slots="slots" :item-height="itemHeight" @change="onAddressChange"
                   :visible-item-count="5"></mt-picker>
      </div>
    </transition>

    <a href="javascript:;" v-show="!show_picker_dialog" class="payBtn fixedBtn" @click="to_pay">马上支付</a>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/order";
  @import "../public/css/form";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  function preHandler(e) {
    e.preventDefault(); // 出现弹框时 禁止滚动
  }

  var _body = document.querySelector('body');

  function stopScroll(e) {
    _body.addEventListener('touchmove', preHandler, {passive: false});
  }

  function openScroll(e) {
    _body.removeEventListener('touchmove', preHandler, {passive: false});
  }

  if (!Object.keys) {
    Object.keys = (function () {
      'use strict';
      var hasOwnProperty = Object.prototype.hasOwnProperty,
        hasDontEnumBug = !({toString: null}).propertyIsEnumerable('toString'),
        dontEnums = [
          'toString',
          'toLocaleString',
          'valueOf',
          'hasOwnProperty',
          'isPrototypeOf',
          'propertyIsEnumerable',
          'constructor'
        ],
        dontEnumsLength = dontEnums.length;

      return function (obj) {
        if (typeof obj !== 'function' && (typeof obj !== 'object' || obj === null)) {
          throw new TypeError('Object.keys called on non-object');
        }

        var result = [], prop, i;

        for (prop in obj) {
          if (hasOwnProperty.call(obj, prop)) {
            result.push(prop);
          }
        }

        if (hasDontEnumBug) {
          for (i = 0; i < dontEnumsLength; i++) {
            if (hasOwnProperty.call(obj, dontEnums[i])) {
              result.push(dontEnums[i]);
            }
          }
        }
        return result;
      };
    }());
  }
  import address from '../public/js/address';

  export default {
    name: 'order',
    data: function () {
      return {
        itemHeight: 60,

        edit:false,
        index:0,
        time:'',
        url:'',
        uid:'',
        btnUrl:'',

        commodity: '肠癌超早期筛查检测',//商品名
        pay: 'wx_payment',// 用什么方式支付
        commodity_num: 1,// 购买数量
        price: 199,// 商品单价


        imageSrc: 'images/commodity_drawing.jpg', //商品图路径
        name: '',
        tel: '',
        address: '请选择地区',
        specific_address: '',//具体地址

        /* 省市区 */
        show_picker_dialog: false,
        slots: [
          {
            flex: 1,
            values: (function () {
              var p = Object.keys(address);
              p.unshift('请选择');
              return p;
            })(),
            className: 'slot1',
            textAlign: 'center'
          },
          {
            divider: true,
            content: '-',
            className: 'slot2'
          },
          {
            flex: 1,
            values: ['请选择'],
            className: 'slot3',
            textAlign: 'center'
          },
          {
            divider: true,
            content: '-',
            className: 'slot4'
          },
          {
            flex: 1,
            values: ['请选择'],
            className: 'slot5',
            textAlign: 'center'
          }
        ],


        currentAddress: { // 最终选择的地址
          province: '',
          city: '',
          town: ''
        },
        addressProvince: [],
        addressCity: [],
        addressTown: [],
        picker: ''

        /* 省市区 */
      }
    },
    watch: {
      address: function (newValue, oldValue) {
        if (newValue === '请选择-请选择-请选择' || newValue === 'undefined-undefined-undefined') {
          this.address = '请选择地区';
        }
      }
    },
    methods: {
      addNum: function () { // 增加购买数量
        this.commodity_num = this.commodity_num + 1
      },
      reduceNum: function () { // 减少购买数量
        this.commodity_num = this.commodity_num - 1;
        if (this.commodity_num <= 1) {
          this.commodity_num = 1
        }
      },
      numInput: function () { // 手动输入购买数量
        var res = +this.commodity_num.replace(/[^0-9]/g, '');
        if (res <= 0) {
          res = 1;
        }
        this.commodity_num = res;
      },
      onAddressChange: function (picker, values) { // 选择省市区
        this.picker = picker;
        // 获得 市
        this.currentAddress.province = values[0];
        var city = address[this.currentAddress.province];
        if (!city) {
          city = ['暂无数据']
        }
        this.addressCity = Object.keys(city);

        if (this.addressCity[0] == '0') {
          this.addressCity = ['请选择'];
        }
        picker.setSlotValues(1, this.addressCity);
        // 获得 区/镇
        this.currentAddress.city = values[1];
        var town = city[this.currentAddress.city];
        if (!town) {
          town = ['暂无数据'];
        }
        this.addressTown = town;
        if (this.addressCity[0] === '请选择') {
          this.addressTown = ['请选择'];
        }
        picker.setSlotValues(2, this.addressTown);
        this.currentAddress.town = values[2];
      },
      hide_picker_dialog: function (e) { // 点击空白处隐藏 省市区
        var _con = $('.picker');   // 设置目标区域
        if (!_con.is(e.target) && _con.has(e.target).length === 0) { // Mark 1
          this.show_picker_dialog = false;
          this.address = this.currentAddress.province + '-' + this.currentAddress.city + '-' + this.currentAddress.town;
          openScroll();
        }
      },
      showPicker: function () { // 显示省市区
        this.show_picker_dialog = true;
        stopScroll();
      },
      inputBlur: function (e) { // 禁止选中
        e.target.blur();
      },
      is_tel: function (phone) {
        return !!phone.match(/^((\d{11})|^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$)$/);
      },
      to_pay: function () { // 支付按钮
        switch (true) {
          case (this.name.trim() === ''):
            this.justTips('姓名不能为空');
            return;
            break;
          case (this.tel.trim() === '' ):
            this.justTips('手机号码不能为空');
            return;
            break;
          case (!this.is_tel(this.tel.trim())):
            this.justTips('手机号码不正确');
            return;
            break;
          case (this.address.trim() === ''):
            this.justTips('地址不能为空');
            return;
            break;
          case (this.address.trim() === '请选择地区'):
            this.justTips('请选择地区');
            return;
            break;
          case (this.specific_address.trim() === ''):
            this.justTips('详细收货地址不能为空');
            return;
            break;
          default:
            break;
        }

        if(this.edit){
          this.$store.state.order.splice(this.index,1,{
            state: 'wait',
            name: this.name.trim(),
            tel: this.tel.trim(),
            address: this.address.trim(),
            specific_address: this.specific_address.trim(),
            title: this.commodity,
            num: this.commodity_num,
            price: this.price,
            time: this.time,
            uid:this.uid,
            currentAddress:this.currentAddress,
            url:this.url, // 商品链接
            btnUrl:this.uid // 按钮连接
          });
        }else {
          let date = new Date();
          let year = date.getFullYear(),
            month = (date.getMonth() + 1),
            day = date.getDate(),
            hours = date.getHours(),
            min = date.getMinutes();
          month = month>=10?month:'0'+month;
          day = day>=10?day:'0'+day;
          hours = hours>=10?hours:'0'+hours;
          min = min>=10?min:'0'+min;
          let uid = Math.floor(Math.random()*100 )+ (+new Date());
          this.$store.state.order.push({
            state: 'wait',
            name: this.name.trim(),
            tel: this.tel.trim(),
            address: this.address.trim(),
            specific_address: this.specific_address.trim(),
            title: this.commodity,
            num: this.commodity_num,
            price: this.price,
            time: year+'-'+month+'-'+day+' '+hours+':'+min,
            uid:uid,
            currentAddress:this.currentAddress,
            url:'javascript:;', // 商品链接
            btnUrl:uid // 按钮连接
          });
        }
        this.edit = false;
        this.$router.push({path:'/myOrder'});
      },
      justTips: function (str) {
        this.$store.commit('show_justTips', {
          str: str.trim(),
          show: true
        });
      }
    },
    created: function () {
      this.addressProvince = Object.keys(address); // 获取省的资料
    },
    components: {},
    mounted: function () {
      let uid = this.$route.query.uid;
      if(uid){
        let list = this.$store.state.order;
        let len = list.length;
        for (let i = 0;i<len;i++){
          if(list[i].uid === uid){
            this.name = list[i].name;
            this.tel = list[i].tel;
            this.commodity_num = list[i].num;
            this.commodity = list[i].title;
            this.specific_address = list[i].specific_address;
            this.price = list[i].price;
            this.address = list[i].address;
            this.currentAddress = list[i].currentAddress;
            this.edit = true;
            this.index = i;
            this.time = list[i].time;
            this.url = list[i].url;
            this.btnUrl = list[i].btnUrl;
            this.uid = list[i].uid;
          }
        }
      }
      this.$nextTick(() => {
        setTimeout(() => {
          this.picker.setSlotValue(0, this.currentAddress.province?this.currentAddress.province:'请选择')
        }, 100)
      });
      document.querySelector('html').classList.add('bg');
    },
    computed: {
      totalPrice: function () { // 价格总计
        return (this.price * this.commodity_num)
      }
    }
  }
</script>



