<template>
  <div>
    <img src="../public/images/code_bg.png" class="head-pic">
    <div class="tab">
      <p>
        <span class="scan" @click="tab" data-text="scan"  :class="{active:active==='scan'}" >扫一扫条码号</span>
        <span class="write" @click="tab" data-text="write"  :class="{active:active!=='scan'}" >手动输入条码号</span>
      </p>

      <div class="tab-con" >
        <transition name="list-left" mode="in-out">
          <input type="button" value="点击扫描条码号" v-show="active==='scan'">
        </transition>
        <transition name="list-left" mode="in-out">
          <input type="text" placeholder="请输入条码号" v-model="barcode" v-show="active!=='scan'">
        </transition>

      </div>
      <p class="where"><span @click="show_dialog">条码号在哪里？</span></p>
    </div>
    <p class="checkTel">验证手机号码</p>
    <div class="handler">
      <label class="tel_input">
        <input  type="tel" v-model="tel" @blur="checkTel"  placeholder="请输入亲友手机号">
      </label>
      <div class="code clearfloat">
        <label>
          <input type="text" @blur="checkCode" v-model="code" placeholder="请输入验证码">
        </label>
        <button @click="sendValidateCode" v-text="vCodeData.text"></button>
      </div>


      <a href="javascript:;" @click="sure" class="btn" :class="{disable:!code_is_ok}" >确定</a>

    </div>


    <transition name="fade">
      <div class="dialog" @click="close_dialog" v-show="showTips">
        <img src="../public/images/where_the_code.png">
      </div>
    </transition>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/code_registration";
  @import "../public/css/login";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  function preHandler(e) {
    e.preventDefault(); // 出现弹框时 禁止滚动
  }
  var _body = document.querySelector('body');
  function stopScroll(e) {
    _body.addEventListener('touchmove', preHandler, {passive: false});
  }
  function openScroll(e) {
    _body.removeEventListener('touchmove', preHandler, {passive: false});
  }


  export default {
    name:'add_relatives_and_friends_report',
    data: function () {
      return {
        active:'scan',
        barcode:'',//条码号
        showTips:false,

        code_is_ok:false,
        // 表单内容
        tel:'',
        code:'',


        vCodeData:{//验证码相关参数
          sending:0,
          s:60,
          text:'获取验证码',
          timer:null
        }
      }
    },
    methods: {
      tab:function (e) { // 切换 扫码 还是 手输
        var text = e.target.getAttribute('data-text');
        this.active = text;
      },
      close_dialog:function () { // 关闭条码号在哪提示
        this.showTips = false;
        openScroll();
      },
      show_dialog:function () { // 显示 条码号在哪提示
        this.showTips = true;
        stopScroll();
      },
      is_tel:function (phone) { // 是否 是正确的手机号
        return !!phone.match(/^((\d{11})|^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$)$/);
      },
      sure:function () { // 更换手机号码
        var tel = this.tel.trim();
        if(!this.code_is_ok){return}
        switch (true){
          case (this.barcode.trim() === ''):
            this.justTips('条码号不能为空');
            return;
            break;
          case (tel === ''):
            this.justTips('手机号码不能为空');
            return;
            break;
          case (!this.is_tel(tel)):
            this.justTips('手机号码格式不正确');
            return;
            break;
          case (this.code === ''):
            this.justTips('验证码不能为空');
            return;
            break;
          default:
            break;
        }
      },
      sendValidateCode:function () {
        if(this.vCodeData.sending){return}
        if(this.checkTel()){
          this.send();
        }
      },
      send:function () {
        clearInterval(this.vCodeData.timer);
        var self=  this;
        this.vCodeData.sending = 1;
        this.vCodeData.timer = setInterval(function () {
          self.vCodeData.s -= 1;
          self.vCodeData.text = self.vCodeData.s + 's';
          if(self.vCodeData.s <= 0){
            self.reset(self);
          }
        },1000);
        return true;
      }, // 倒计时
      reset:function (self) {
        clearInterval(self.vCodeData.timer);
        self.vCodeData.text = '获取验证码';
        self.vCodeData.s = 60;
        self.vCodeData.sending = 0
      }, // 倒计时重置
      checkTel:function () {
        var tel = this.tel.trim();
        switch (true){
          case (tel === ''):
            this.justTips('手机号码不能为空');
            return false;
            break;
          case (!this.is_tel(tel)):
            this.justTips('手机号码格式不正确');
            return false;
            break;
          default:
            break;
        }
        return true;
      },
      checkCode:function () { // 验证码是否正确
        var code = this.code.trim();
        if(code){
          this.code_is_ok = true;
        }else {
          this.code_is_ok = false;
        }
      },
      justTips:function (str) {
        this.$store.commit('show_justTips', {
          str:str.trim(),
          show:true
        });
      }
    },
    mounted(){
      document.querySelector('html').classList.remove('bg')
    },
  }
</script>



