<template>
  <div class="">
    <main>
      <ul class="list-form">
        <li>
          <span>姓名</span>
          <div class="user_pic_h"></div>
          <p class="user_pic_wrap">
            <!--用户头像-->
            <img src="../public/images/user_info_portrait.png" class="user_pic">
            <!--箭头-->
            <img src="../public/images/user_info_icon_right.png" class="user_pic_icon">
          </p>
        </li>
        <li>
          <span>姓名</span>
          <label>
            <input type="text" v-model="user_name" placeholder="请输入真实姓名" maxlength="15">
          </label>
        </li>
        <li>
          <span>微信昵称</span>
          <label>
            <span class="justText" v-text="nickname"></span>
          </label>
        </li>
        <li>
          <span>性别</span>
          <label>
            <select v-model="sex">
              <option disabled>请选择性别</option>
              <option>男</option>
              <option>女</option>
            </select>
            <input type="text" placeholder="请选择性别" v-model="sex" class="disable">
          </label>
        </li>
        <li>
          <span>更换手机号码</span>
          <router-link to="/change_tel" class="change_tel">
            <span class="justText" v-text="tel"></span>
            <!--箭头-->
            <img src="../public/images/user_info_icon_right.png" class="user_pic_icon">
          </router-link>
        </li>
      </ul>
      <a href="javascript:;" class="next save">保存信息</a >
    </main>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/form";
  @import "../public/css/user_info";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name:'user_info',
    data: function () {
      return {
        // 表单内容
        user_name: '你的名字',
        nickname: '小番茄',
        sex: '男',
        tel: '123456789'
      }
    },
    created: function () {

    },
    mounted(){
      document.querySelector('html').classList.add('bg')
    },
    methods: {}
  }
</script>



