// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router';
import './public/js/jsrem';
import './public/js/jquery.min';
import MintUI from 'mint-ui';
import Vuex from 'vuex';


Vue.use(Vuex);
Vue.use(MintUI);
Vue.use(require('vue-wechat-title'));
const store = new Vuex.Store({
  state:{
    justTips:{
      show:false,
      str:'',
      timer:null,
      duration:2500,
    },
    form:{
      name:'',
      sex:'',
      birth:'',
      sampling_date:'',
      family_history:'',
      current_history:'',
      tel:''
    },
    order:[]
  },
  mutations:{
    show_justTips:function (state, options) {
      clearTimeout(state.justTips.timer);
      state.justTips.show = true;
      state.justTips.str = options.str;


      state.justTips.timer = setTimeout(function () {
        state.justTips.show = false;
      },state.justTips.duration);
    }
  }
});


Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});



// 显示loading
function loadingOpen(str) {
  MINT.Indicator.open({
    text: str,
    spinnerType: 'snake'
  });
  stopScroll();
}
// 关闭loading
function loadingClose() {
  MINT.Indicator.close();
  openScroll();
}



