<template>
  <div class="mainbox">
    <div class="index-banner"><img src="../public/images/home_banner.jpg"></div>
    <div class="index-btns">
      <div class="btn-mydoctor ">
					<span>
						<a href="javascript:;">我的医生</a>
					</span>
      </div>
      <dl>
        <dd class="bot-line right-line"><router-link to="/form" class="btn-write"><span>登记信息</span></router-link></dd>
        <dd class="bot-line"><router-link  to="/myReport"  class="btn-view"><span class="hasNew">查看报告</span></router-link></dd>
        <dd class="bot-line right-line"><router-link  to="/order"  class="btn-buy"><span>购买产品</span></router-link ></dd>
        <dd class="bot-line"><router-link  to="/myOrder"  class="btn-order"><span>我的订单</span></router-link ></dd>
        <dd class="right-line"><router-link  to="/user_info"  class="btn-my"><span>个人信息</span></router-link ></dd>
      </dl>
    </div>
  </div>
</template>
<style lang="scss" scoped>

  //定义各种颜色
  $blackColor:#333;
  $grayColor:#666;
  $lightgrayColor:#999;
  $whiteColor:#fff;
  $blueColor:#24A6DF;
  $redColor:#ff2626;
  $orangeColoe:#FF7F00;
  $grayBgColor:#ccc;
  $lightGrayBgColor:#f1f3f4;
  $bodyBgColor:#fff;
  $grayBorder:#d9d9d9;
  $orangeBtnBg:#FF9326;
  $orangeBtnBg2:#FF7A4D;
  $grayBtnBg:#A4A4A4;
  $blueBtnBg:#24A6DF;

  //定义字体大小
  $normalSize:0.46rem;
  $bigSize:0.5rem;
  $smallSize:0.42rem;
  $smallerSize:0.36rem;


  .index-banner {
    img {
      display: block;
      width: 100%;
    }
  }

  .index-btns {
    position: relative;
    z-index: 2;
    padding: 2rem 0.5rem 0.5rem 0.5rem;

    .btn-mydoctor {
      position: absolute;
      z-index: 2;
      top: -1.6rem;
      left: 50%;
      margin-left: -1.6rem;
      width: 3.2rem;
      height: 3.2rem;

      span {
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 0.14rem;

        a {
          position: relative;
          z-index: 2;
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: url(../public/images/icon_echometer.png) no-repeat center 0.5rem;
          background-size: 33% auto;
          background-color: rgba(36, 166, 223, 1);
          box-sizing: border-box;
          color: #fff;
          font-size: $bigSize;
          text-align: center;
          padding: 1.7rem 0 0 0;
        }
      }

      &::after {
        content: "";
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: rgba(36, 166, 223, 1);
        box-sizing: border-box;

      }
      &.newTips::after {
        -webkit-animation: newTips 1.5s 0s linear infinite;
      }
    }

    dl {
      overflow: hidden;
      dd {
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        float: left;
        width: 50%;
        text-align: center;
        position: relative;

        &.bot-line {
          border-bottom: 1px solid $grayBorder;
        }
        &.right-line {
          border-right: 1px solid $grayBorder;
        }

        a {
          display: block;
          width: 100%;
          position: relative;
          line-height: 0.8rem;
          font-size: $normalSize;
          padding: 1.6rem 0 0.3rem 0;
          background-position: center 0.4rem;
          background-repeat: no-repeat;
          background-size: auto 44%;
          color: $blueColor;

          &:active {
            background-color: $lightGrayBgColor;
          }

          &.btn-write {
            background-image: url(../public/images/icon_write.png);
          }
          &.btn-view {
            background-image: url(../public/images/icon_view.png);
          }
          &.btn-buy {
            background-image: url(../public/images/icon_buy.png);
          }
          &.btn-order {
            background-image: url(../public/images/icon_order.png);
          }
          &.btn-my {
            background-image: url(../public/images/icon_my.png);
          }

          span {
            position: relative;
            display: inline-block;
            padding: 0 0.4rem;

            &.hasNew::after {
              content: "";
              position: absolute;
              right: 0;
              top: 50%;
              width: 0.24rem;
              height: 0.24rem;
              margin-top: -0.12rem;
              background-image: url(../public/images/redPoint.png);
              background-position: center center;
              background-repeat: no-repeat;
              background-size: 100% auto;
              overflow: hidden;
            }
          }
        }
      }
    }
  }

  @-webkit-keyframes newTips {
    0% {
      -webkit-transform: scale(1, 1);
      opacity: 0;
    }
    50% {
      opacity: 0.6;
    }
    100% {
      -webkit-transform: scale(1.1, 1.1);
      opacity: 0;
    }
  }
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name:'home',
    mounted(){
      document.querySelector('html').classList.remove('bg')
    },
  }
</script>



