<template>
  <div>
    <main v-if="lists.length>0" class="my-order">

      <transition-group name="list" tag="ul">
        <li v-for="(list,index) in lists" :key="''+list">
          <a :href="list.url" class="clearfloat commodity  center">
            <div class="f_left">
              <img src="../public/images/commodity_drawing.jpg">
            </div>
            <div class="f_right">
              <div class="f_right_title">{{list.title}}</div>
              <div class="">下单时间：{{list.time}}</div>
              <div class="">
                价格：<span>{{list.num * list.price}}</span>元
              </div>
              <div class="">
                购买数量：<span>{{list.num}}</span>份
              </div>
            </div>
          </a>

          <div class="commodity_state">
            <div class="state_msg">
              状态 : <span :class="{wait:list.state === 'wait'}">{{list.state|commodityState}}</span>
            </div>
            <div class="payBtn clearfloat" v-if="list.state === 'wait'">
              <router-link :to="{path:'/order',query:{uid:list.btnUrl}}">马上支付</router-link>
            </div>
          </div>
        </li>
      </transition-group>
      <p class="lists_bottom_tips">
        如有疑问，敬请咨询“便宝宝”公众号
      </p>
    </main>
    <router-link v-if="lists.length>0"  to="/order" class=" fixedBtn" >继续下单</router-link>

    <div v-if="lists.length===0" class="no-order">
      <img src="../public/images/no_order.png" class="no-order-icon">
      <p>您还没有任何订单</p>
      <router-link to="order" href="javascript:;">马上下单</router-link>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/order";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name:'myOrder',
    data: function () {
      return{
        lists:[
          {
            title:'肠癌超早期筛查检测',
            time:'2017-06-26 16:50',// 下单时间
            price:199,// 商品单价,
            num:3,//购买数量
            imageSrc:'images/commodity_drawing.jpg', //商品图路径,
            state:'wait',// 订单状态, //
            url:'javascript:;', // 商品链接
            btnUrl:'javascript:;' // 按钮连接
          },
        ]
      }
    },
    watch:{

    },
    methods: {},
    created: function () {
       this.lists = this.$store.state.order;
    },
    filters:{
      commodityState:function (val) { // 订单状态
        switch (true){
          case (val === 'wait'):
            return '待付款';
            break;
          case (val === 'done'):
            return '已完成';
            break;
          default:
            break;
        }
      }
    },
    components:{

    },
    mounted(){
      document.querySelector('html').classList.add('bg')
    },
    computed: {

    }
  }
</script>



