<template>
  <div class="">
    <main>
      <h2>请输入受检人基本信息</h2>
      <ul class="list-form">
        <li>
          <span><b class="required">*</b>姓名</span>
          <label>
            <input @focus="hideBtn" @blur="showBtn" type="text" v-model="name" placeholder="请输入受检人姓名" maxlength="15">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>性别</span>
          <label>
            <select v-model="sex" @focus="hideBtn" @blur="showBtn">
              <option disabled>请选择性别</option>
              <option>男</option>
              <option>女</option>
            </select>
            <input type="text" placeholder="请选择性别" v-model="sex" class="disable">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>出生日期</span>
          <label for="birth">
            <input @focus="hideBtn" @blur="showBtn" type="date" placeholder="请选择出生日期" class="hide-input" id="birth"
                   v-model="birth">
            <input type="text" placeholder="请选择出生日期" class="disable" v-model="birth">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>采样日期</span>
          <label for="sampling_date">
            <input @focus="hideBtn" @blur="showBtn" type="date" placeholder="请选择采样日期" class="hide-input"
                   id="sampling_date" v-model="sampling_date">
            <input type="text" placeholder="请选择采样日期" class="disable" v-model="sampling_date">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>家族病史</span>
          <label>
            <input type="text" @blur="showBtn" @focus="hideBtn" v-model="family_history" placeholder="无病史请填无"
                   maxlength="50">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>现有疾病</span>
          <label>
            <input @focus="hideBtn" @blur="showBtn" type="text" v-model="current_history" placeholder="无病史请填无"
                   maxlength="50">
          </label>
        </li>
        <li>
          <span><b class="required">*</b>手机号码</span>
          <label>
            <input @focus="hideBtn" @blur="showBtn" type="tel" disabled v-model="tel" placeholder="请填写手机号码"
                   class="disable">
          </label>
        </li>
      </ul>

      <p class="lists_bottom_tips">
        丰富以上信息有利于我们为您追查报告
      </p>
      <transition name="fade">
        <a @click="next" href="javascript:;" class="next ">下一步</a>
      </transition>
    </main>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/form";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name: 'form',
    data: function () {
      return {
        // 表单内容
        name: '',
        sex: '',
        birth: '',
        sampling_date: '',
        family_history: '',
        current_history: '',
        tel: '123456789',


        btn: true,
        timer: null
      }
    },
    created: function () {
      this.sampling_date = this.conversionTime(new Date());
    },
    methods: {
      conversionTime: function (date) { //日期转换
        var _date = date;
        var year, month, day;
        year = _date.getFullYear();
        month = (Number(_date.getMonth()) + 1) > 10 ? (Number(_date.getMonth()) + 1) : '0' + (Number(_date.getMonth()) + 1);
        day = Number(_date.getDate()) > 10 ? _date.getDate() : '0' + _date.getDate();
        return year + '-' + month + '-' + day;
      },
      checkForm: function () { // 检查表单是否有空漏
        switch (true) {
          case (this.name.trim() === ''):
            this.justTips('姓名不能为空');
            return false;
            break;
          case (this.sex.trim() === ''):
            this.justTips('性别不能为空');
            return false;
            break;
          case (this.birth.trim() === ''):
            this.justTips('出生日期不能为空');
            return false;
            break;
          case (this.sampling_date.trim() === ''):
            this.justTips('采样日期不能为空');
            return false;
            break;
          case (this.family_history.trim() === ''):
            this.justTips('家族病史不能为空');
            return false;
            break;
          case (this.current_history.trim() === ''):
            this.justTips('现有疾病不能为空');
            return false;
            break;
          case (this.tel.trim() === ''):
            this.justTips('手机号码不能为空');
            return false;
            break;
          default:
            return true;
            break;
        }
      },
      next: function () { // 确定按钮
        if(this.checkForm()){
          this.$store.state.form = {
            name:this.name.trim(),
            sex:this.sex.trim(),
            birth:this.birth.trim(),
            sampling_date:this.sampling_date.trim(),
            family_history:this.family_history.trim(),
            current_history:this.current_history.trim(),
            tel:this.tel.trim()
          };
          this.$router.push('/check_form')
        }
      },
      hideBtn: function () {
        this.btn = false;
      },
      showBtn: function () {
        clearTimeout(this.timer);
        var self = this;
        this.timer = setTimeout(function () {
          self.btn = true;
        }, 250);
      },
      justTips: function (str) {
        this.$store.commit('show_justTips', {
          str: str.trim(),
          show: true
        });
      }
    },
    mounted(){
      document.querySelector('html').classList.remove('bg')
    },
  }
</script>



