<template>
  <div class="ViewReport">
    <header>
      <table>
        <tr>
          <td class="border-r border-bottom color">姓名</td>
          <td class="border-bottom color">条码号</td>
        </tr>
        <tr>
          <td class="border-r">{{userName}}</td>
          <td>{{barcode}}</td>
        </tr>
      </table>
    </header>
    <main>
      <div class="con">
        <section v-for="list in lists" :key="''+list" :class="list.state">
          <p>
            {{list.text}}
            <b v-if="list.timeTips">
              <em>{{list.timeTips}}</em>
            </b>
            <span class="time">
                        {{list.time}}
                    </span>
          </p>
        </section>
      </div>
    </main>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/viewReport";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name:'view_report',
    data: function () {
      return {
        lists: [
          {
            text: '客户录入数据',
            time: '2017-06-18 17:45',
            state: 'done' // 该流程是否已完成
          },
          {
            text: '样本已签收',
            time: '2017-06-19 22:30',
            state: 'done'
          },
          {
            text: '诊断中心检测',
            time: '2017-06-20 12:34',
            state: 'done',
            timeTips:'约5天'
          },
          {
            text: '审核检测数据',
            time: '2017-06-25 20:45',
            state: 'done',
            timeTips:'约4天'
          },
          {
            text: '结果分析',
            time: '2017-06-30 10:45',
            state: 'done',
            timeTips:'约1天'
          },
          {
            text: '已出具电子报告',
            time: '',
            state: ''
          }
        ],
        barcode: '123456789', //条码号
        userName: '你的名字',
      }
    }
  }
</script>



