import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login';
import Home from '@/components/home';
import Form from '@/components/form';
import Check_form from '@/components/check_form';
import report from '@/components/report';
import view_report from '@/components/view_report';
import add_relatives_and_friends_report from '@/components/add_relatives_and_friends_report';
import order from '@/components/order';
import myOrder from '@/components/myOrder';
import user_info from '@/components/user_info';
import change_tel from '@/components/change_tel';
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      meta: {
        title: '登录'
      },
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      meta: {
        title: '首页'
      },
      component: Home
    },
    {
      path: '/form',
      name: 'form',
      meta: {
        title: '登记信息'
      },
      component: Form
    },
    {
      path: '/check_form',
      name: 'check_form',
      meta: {
        title: '检查登记信息'
      },
      component: Check_form
    },

    {
      path: '/myReport',
      name: 'myReport',
      meta: {
        title: '查看报告'
      },
      component: report,
    },
    {
      path: '/view_report',
      name: 'view_report',
      meta: {
        title: '报告进度'
      },
      component: view_report
    },
    {
      path: '/add_relatives_and_friends_report',
      name: 'add_relatives_and_friends_report',
      meta: {
        title: '新增亲友报告'
      },
      component: add_relatives_and_friends_report
    },
    {
      path: '/order/',
      name: 'order',
      meta: {
        title: '下单'
      },
      component: order
    },
    {
      path: '/myOrder',
      name: 'myOrder',
      meta: {
        title: '我的订单'
      },
      component: myOrder
    },
    {
      path: '/user_info',
      name: 'user_info',
      meta: {
        title: '个人信息'
      },
      component: user_info
    },
    {
      path: '/change_tel',
      name: 'change_tel',
      meta: {
        title: '变更手机号码'
      },
      component: change_tel
    },
  ]
})



