<template>
  <div class="">
    <main>
      <h2>请确定受检人基本信息</h2>
      <ul class="list-form">
        <li>
          <span>姓名</span>
          <label>
            <span class="justText">{{name}}</span>
          </label>
        </li>
        <li>
          <span>性别</span>
          <label>
            <span class="justText">{{sex}}</span>
          </label>
        </li>
        <li>
          <span>出生日期</span>
          <label>
            <span class="justText">{{birth}}</span>
          </label>
        </li>
        <li>
          <span>采样日期</span>
          <label>
            <span class="justText">{{sampling_date}}</span>
          </label>
        </li>
        <li>
          <span class="v-baseline">家族病史</span>
          <label class="v-baseline">
            <span class="justText_auto_height" >{{family_history}}</span>
          </label>

          <!--<span :class="{'v-baseline':family_history.trim().length>=13}">家族病史</span>-->
          <!--<label>-->
          <!--<span class="" :class="[family_history.trim().length>=13?'justText_auto_height':'justText']">{{family_history}}</span>-->
          <!--</label>-->
        </li>
        <li>
          <!--<span :class="{'v-baseline':current_history.trim().length>13}">现有疾病</span>-->
          <!--<label>-->
          <!--<span class="" :class="[current_history.trim().length>13?'justText_auto_height':'justText']">{{current_history}}</span>-->
          <!--</label>-->
          <span class="v-baseline">现有疾病</span>
          <label class="v-baseline">
            <span class="justText_auto_height">{{current_history}}</span>
          </label>
        </li>
        <li>
          <span>手机号码</span>
          <label>
            <span class="justText">{{tel}}</span>
          </label>
        </li>
      </ul>
      <div class="btn-wrap clearfloat">
        <router-link to="/form" class="back">返回修改</router-link>
        <a href="javascript:;" class="sure" @click="sure">确定提交</a>
      </div>
    </main>
  </div>
</template>
<style lang="scss" scoped>
  @import "../public/css/form";
</style>
<style lang="scss">
  @import "../public/css/style.scss";
</style>
<script>
  export default {
    name:'check_form',
    data:function () {
      return{
        // 表单内容
        name:'你的名字',
        sex:'女',
        birth:'2017-07-11',
        sampling_date:'2017-07-11',
        family_history:'试试撒是是是是是是是是是是是是是是是是是是是是是是是是撒是是是是是是是是是是是是是是是是是是是是是是是是所所所所',
        current_history:'我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是我是发是啊啊是发是是我我是',
        tel:'123456789',
      }
    },
    created:function () {
      let form = this.$store.state.form;
      if(form.name.trim()===''){
        this.justTips('请先填写信息');
        this.$router.replace('/form')
      }
      for(let key in form){
        if(form.hasOwnProperty(key)){
          this[key] = form[key];
        }
      }
    },
    mounted(){
      document.querySelector('html').classList.remove('bg')
    },
    methods:{
      justTips: function (str) {
        this.$store.commit('show_justTips', {
          str: str.trim(),
          show: true
        });
      },
      sure:function () {
        this.justTips('提交成功');
        this.$router.replace('/home')
      }
    }
  }
</script>



